            </el-form-item>
            <el-form-item>
              <el-input placeholder="供应类别"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input placeholder="您的称呼"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input placeholder="您的职务"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input placeholder="您的邮箱"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input placeholder="您的电话"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input placeholder="备注"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button>提交</el-button>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
    </el-col>
  </el-row>
</template>

<script>/* eslint-disable */
export default {
  name: 'Consult'
}
</script>

<style scoped>
  #image{
    background-image: url('../assets/20180919014000813.jpg');
    height: 500px;
  }
  #consult{
    margin: 30px 0;
    padding: 10px 30px;
    border-radius: 10px;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04)
  }
  h1{
    margin: 10px 0;
  }
  p{
    margin: 10px 0;
  }
</style>



