<template>
  <el-row>
    <el-row id="image">
    </el-row>
    <el-col offset="4" span="16">
      <el-row id="create-provider">
        <el-col :span="12">
          <img src="../assets/logo.png">
        </el-col>
        <el-col :span="12">
          <el-form id="provider-form">
            <el-row>成为供应商</el-row>
            <el-row>请填写您的信息，专业顾问将与您沟通！</el-row>
            <el-form-item>
              <el-input placeholder="您的公司"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input placeholder="供应类别"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input placeholder="您的称呼"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input placeholder="您的职务"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input placeholder="您的邮箱"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input placeholder="您的电话"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input placeholder="备注"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button>提交</el-button>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
    </el-col>
  </el-row>
</template>

<script>/* eslint-disable */
  export default {
    name: 'CreateProvider'
  }
</script>

<style scoped>
  #image{
    background-image: url('../assets/20180919014000813.jpg');
    height: 500px;
  }
  #create-provider{
    margin: 20px 0;
    background-color: rgba(159, 159, 159, 0.78);
    padding: 10px 10px;
    text-align: center;
    border-radius: 10px;
  }
</style>



